.icon {
  position: relative;

  display: block;
  width: 100%;
  padding-bottom: 100%;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);

    display: block;
    width: max-content;
    padding-right: 20px;

    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    color: $colorClear;
    letter-spacing: 2px;
    text-transform: uppercase;
    opacity: 0;
    pointer-events: none;
  }

  @include onDesktop {

    &:hover &__tooltip {
      opacity: 1;
      pointer-events: all;

      transition: opacity 0.3s;
    }
  }

  &--menu {
    background-image: url(../images/Burger-menu.svg);

    &:hover {
      background-image: url(../images/Burger-menu-hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/Icon-Phone-call.svg);
  }

  &--close {
    background-image: url(../images/Icon-Close.svg);
  }
}
